
'use client';

import React, { useState, useEffect } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { db } from '@/lib/firebase';
import { doc, getDoc } from 'firebase/firestore';
import type { Lot, User } from '@/functions/src/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import CountdownBadge from '@/components/ui/countdown-badge';
import StarRating from '@/components/ui/star-rating';
import { Skeleton } from '@/components/ui/skeleton';

interface LotCardProps {
  lot: Lot;
}

const LotCard: React.FC<LotCardProps> = ({ lot }) => {
  const [seller, setSeller] = useState<User | null>(null);
  const [loadingSeller, setLoadingSeller] = useState(true);

  useEffect(() => {
    const fetchSeller = async () => {
      if (lot.sellerUid) {
        try {
          const sellerDoc = await getDoc(doc(db, 'users', lot.sellerUid));
          if (sellerDoc.exists()) {
            setSeller(sellerDoc.data() as User);
          }
        } catch (error) {
          console.error("Error fetching seller:", error);
        } finally {
          setLoadingSeller(false);
        }
      }
    };
    fetchSeller();
  }, [lot.sellerUid]);

  const imageUrl = lot.images && lot.images.length > 0 ? lot.images[0] : '/placeholder.png';

  return (
    <Card className="overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 ease-in-out hover:scale-[1.03] transform rounded-[12px] break-inside-avoid flex flex-col">
      <Link href={`/lot/${lot.id}`} aria-label={`Переглянути деталі лоту ${lot.name}`} className="flex-grow">
        <CardHeader className="p-0">
          <div className="aspect-[4/3] relative w-full">
            <Image
              src={imageUrl}
              alt={lot.name}
              fill
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              className="object-cover"
            />
          </div>
        </CardHeader>
        <CardContent className="p-4 flex-grow">
          <CardTitle className="text-lg font-headline mb-2 truncate" title={lot.name}>{lot.name}</CardTitle>
          <div className="flex justify-between items-center mb-2">
            <p className="text-sm text-muted-foreground">Поточна ставка:</p>
            <p className="text-xl font-semibold text-primary">{lot.currentBid} грн</p>
          </div>
          <CountdownBadge endTime={new Date(lot.endTime)} />
        </CardContent>
      </Link>
      <CardFooter className="p-4 pt-0 border-t mt-auto">
        {loadingSeller ? (
            <div className="flex items-center gap-2 w-full">
                <Skeleton className="h-8 w-8 rounded-full" />
                <div className="w-full space-y-1">
                    <Skeleton className="h-4 w-2/3" />
                    <Skeleton className="h-3 w-1/3" />
                </div>
            </div>
        ) : seller ? (
            <div className="flex items-center justify-between w-full">
                <div className="flex items-center gap-2">
                    <Avatar className="h-8 w-8">
                        <AvatarImage src={seller.photoURL || undefined} alt={seller.username} />
                        <AvatarFallback>{seller.username?.[0].toUpperCase()}</AvatarFallback>
                    </Avatar>
                    <div>
                        <p className="text-sm font-medium leading-none">{seller.username}</p>
                        {seller.sellerRating && (
                             <div className="flex items-center gap-1">
                                <StarRating rating={seller.sellerRating} />
                                <span className="text-xs text-muted-foreground">({seller.sellerRating.toFixed(1)})</span>
                             </div>
                        )}
                    </div>
                </div>
                <Button variant="outline" size="sm" asChild>
                    <Link href={`/lot/${lot.id}`}>Ставка</Link>
                </Button>
            </div>
        ) : null}
      </CardFooter>
    </Card>
  );
};

export default LotCard;
