
import React from 'react';
import { Star } from 'lucide-react';
import { cn } from '@/lib/utils';

interface StarRatingProps {
  rating: number;
  totalStars?: number;
  className?: string;
  starClassName?: string;
}

const StarRating: React.FC<StarRatingProps> = ({ 
  rating, 
  totalStars = 5, 
  className,
  starClassName 
}) => {
  const fullStars = Math.floor(rating);
  const halfStars = Math.round(rating) - fullStars;
  const emptyStars = totalStars - fullStars - halfStars;

  return (
    <div className={cn("flex items-center", className)}>
      {[...Array(fullStars)].map((_, i) => (
        <Star key={`full-${i}`} className={cn("h-4 w-4 fill-yellow-400 text-yellow-400", starClassName)} />
      ))}
      {[...Array(halfStars)].map((_, i) => (
        <Star key={`half-${i}`} className={cn("h-4 w-4", starClassName)} style={{ fill: 'url(#half_grad)' }} />
      ))}
      {[...Array(emptyStars)].map((_, i) => (
        <Star key={`empty-${i}`} className={cn("h-4 w-4 text-gray-300", starClassName)} />
      ))}
      <svg width="0" height="0" style={{ position: 'absolute' }}>
        <defs>
          <linearGradient id="half_grad" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="50%" style={{ stopColor: 'rgb(250 204 21)', stopOpacity: 1 }} />
            <stop offset="50%" style={{ stopColor: 'rgb(209 213 219)', stopOpacity: 1 }} />
          </linearGradient>
        </defs>
      </svg>
    </div>
  );
};

export default StarRating;
